
.taskContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
}


.taskApp {
  width: 50vw;
  height: 50vh;
  margin-top: 1rem;
}


.taskCloseBtn {
  display: flex;
  justify-content: flex-end;
}


.taskCloseBtn button {
  background-color: transparent;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  color: rgb(40, 40, 40, 0.8);
}
  @media screen and (min-width: 1024px) {
    .taskCloseBtn button {
      font-size: 1.15rem;
  }
}
  @media screen and (min-width: 1440px) {
    .taskCloseBtn button {
      font-size: 1.25rem;
  }
}
  @media screen and (min-width: 2560px) {
    .taskCloseBtn button {
      font-size: 1.5rem;
  }
}


.taskCompiler {
  margin-bottom: 5px;
  text-align: center;
  font-family: 'Bakbak One', sans-serif;
  font-size: 1rem;
  color: rgb(40, 40, 40, 0.8);
}
  @media screen and (min-width: 375px) {
    .taskCompiler {
      font-size: 1.5rem;
  }
}
  @media screen and (min-width: 768px) {
    .taskCompiler {
      font-size: 2rem;
  }
}
  @media screen and (min-width: 1440px) {
    .taskCompiler {
      font-size: 2.15rem;
  }
}
  @media screen and (min-width: 2560px) {
    .taskCompiler {
      font-size: 3rem;
  }
}


.horizontalLine {
  border-color: rgb(40, 40, 40, 0.8);
  width: 55vw;
}


.taskForm {
  display: flex;
  flex-direction: row;
  flex-direction: row;
  position: relative;
}


.taskInput {
  position: relative;
  margin: auto;
  margin-top: 5px;
  margin-bottom: 20px;
  padding: .3rem;
  border: 2px solid rgb(40, 40, 40, 0.8);
  background: rgb(40, 40, 40, 0.5);
  color: #F7EF8A;
  font-family: 'Play', sans-serif;
  font-weight: 400;
  font-size: 1rem;
}
  @media screen and (min-width: 768px) {
    .taskInput {
      font-size: 1.25rem;
  }
}
  @media screen and (min-width: 1440px) {
    .taskInput {
      padding: .5rem;
      font-size: 1.5rem;
  }
}
  @media screen and (min-width: 2560px) {
    .taskInput {
      font-size: 1.75rem;
  }
}


.taskInput::placeholder {
  color: cornsilk;
  font-family: 'Play', sans-serif;
  font-weight: 400;
  font-size: 1rem;
}
  @media screen and (min-width: 768px) {
    .taskInput::placeholder {
      font-size: 1.25rem;
    
  }
}
  @media screen and (min-width: 1440px) {
    .taskInput::placeholder {
      font-size: 1.5rem;
    }
}
  @media screen and (min-width: 2560px) {
    .taskInput::placeholder {
      font-size: 1.75rem;
    }
}

