.taskRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  margin-top: 10px;
  padding: .3rem .5rem .3rem .5rem;
  border: 2px solid rgb(40, 40, 40, 0.8);
  background: rgb(40, 40, 40, 0.5);
  color: #F7EF8A;
  cursor: pointer;
  font-family: 'Play', sans-serif;
  font-weight: 400;
  font-size: 1rem;
}
  @media screen and (min-width: 768px) {
    .taskRow {
      font-size: 1rem;
  }
}
  @media screen and (min-width: 1440px) {
    .taskRow {
      padding: 1rem 2rem 1rem 2rem;
      font-size: 1.25rem;
  }
}
  @media screen and (min-width: 2560px) {
    .taskRow {
      padding: 1.5rem 2rem 1.5rem 2rem;
      font-size: 1.5rem;
  }
}


.complete {
  text-decoration: line-through;
  opacity: 0.5;
}


.icons {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
}
  @media screen and (min-width: 768px) {
    .icons {
      font-size: 1rem;
  }
}
  @media screen and (min-width: 1440px) {
    .icons {
      font-size: 1.25rem;
  }
}
  @media screen and (min-width: 2560px) {
    .icons {
      font-size: 3rem;
  }
}


.editIcon {
  margin-right: 7px;
  color: #F7EF8A;
}


.removeIcon {
  color: #F7EF8A;
}

